import React, { Component } from 'react';
import { Form, Table, Row, Col, Button, Checkbox, message } from 'antd';
import { ColumnProps, TableEventListeners } from 'antd/lib/table';
import './InfoConfig.less';

import request from '@/request';

interface List {
  id: string;
  name: string;
  x: number;
  y: number;
}
interface Data {
  [time: string]: boolean;
}
interface IProps {
  form: any;
}
interface IState {
  list: List[];
  data: Data;
}

class InfoConfig extends Component<IProps, IState> {
  public state = {
    list: [
      {
        id: 'time',
        name: '时间',
        x: 10,
        y: 90
      },
      {
        id: 'location',
        name: '地点',
        x: 10,
        y: 170
      },
      {
        id: 'runDirection',
        name: '行驶方向',
        x: 10,
        y: 250
      },
      {
        id: 'laneNumber',
        name: '车道号',
        x: 10,
        y: 330
      },
      {
        id: 'action',
        name: '行为',
        x: 10,
        y: 410
      },
      {
        id: 'plateNumber',
        name: '车牌号码',
        x: 10,
        y: 490
      },
      {
        id: 'plateColor',
        name: '车牌颜色',
        x: 10,
        y: 570
      },
      {
        id: 'speed',
        name: '速度',
        x: 10,
        y: 650
      },
      {
        id: 'deviceId',
        name: '设备ID',
        x: 10,
        y: 730
      },
      {
        id: 'redLightStartTime',
        name: '红灯开始时间',
        x: 10,
        y: 810
      },
      {
        id: 'redLightDuration',
        name: '红灯持续时间',
        x: 10,
        y: 890
      },
      {
        id: 'runRedLightTime',
        name: '闯红灯时间',
        x: 10,
        y: 970
      },
      {
        id: 'measurementSign',
        name: '测试标识',
        x: 10,
        y: 1050
      }
    ],
    data: {} as Data
  };
  /**
   * componentDidMount
   */
  public componentDidMount() {
    this.getData();
  }
  /**
   * getData
   */
  public getData() {
    request
      .config({
        url: '/settings/overlay'
      })
      .then((res: any) => {
        this.setState({
          data: res.data
        });
      });
  }
  /**
   * handleCancel
   */
  public handleCancel = () => {
    const { resetFields } = this.props.form;
    resetFields();
  };
  public handleSubmit = () => {
    const { getFieldsValue } = this.props.form;
    const data = getFieldsValue();
    request
      .config({
        url: `/settings/overlay`,
        type: 'post',
        data: data
      })
      .then((res: any) => {
        if (res.code === 0) {
          message.info('保存成功!');
          this.getData();
        } else {
          message.info(res.message);
        }
      });
  };
  /**
   * handleTableRowClick
   */
  public handleTableRowClick = (record: List, event: React.SyntheticEvent) => {
    const { setFieldsValue, getFieldsValue } = this.props.form;
    const data = getFieldsValue();
    setFieldsValue({
      [record.id]: !data[record.id]
    });
  };
  public render() {
    const { data, list } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns: ColumnProps<List>[] = [
      {
        title: '名称',
        dataIndex: 'name',
        key: 'name',
        render(text: string, records: List) {
          return <Form.Item>{getFieldDecorator(records.id, { valuePropName: 'checked', initialValue: data[records.id] })(<Checkbox>{text}</Checkbox>)}</Form.Item>;
        }
      },
      {
        title: 'x',
        dataIndex: 'x',
        key: 'x'
      },
      {
        title: 'y',
        dataIndex: 'y',
        key: 'y'
      }
    ];
    return (
      <Form>
        <Table<List>
          rowKey="id"
          size="small"
          style={{ width: '62.5%' }}
          pagination={false}
          columns={columns}
          dataSource={list}
          onRow={(record, index) => {
            return {
              onClick: this.handleTableRowClick.bind(this, record)
            } as TableEventListeners;
          }}
        />
        <Row style={{ marginTop: 10, textAlign: 'center' }}>
          <Col span={15}>
            <Form.Item>
              <Button icon="check" type="primary" style={{ marginRight: 80 }} onClick={this.handleSubmit}>
                确定
              </Button>
              <Button icon="close" onClick={this.handleCancel}>
                取消
              </Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default Form.create({ name: 'InfoConfig' })(InfoConfig);
