import React, { Component } from 'react';
import { Tabs } from 'antd';

import { withRouter, RouteComponentProps } from 'react-router-dom';
import LPRConfig from './pageComponents/LPRConfig';
import InfoConfig from './pageComponents/InfoConfig';
import CrossingsInfoConfig from './pageComponents/CrossingsInfoConfig';

import './Setting.less';

class IllegalSetting extends Component<RouteComponentProps> {
  /**
   * handleClick
   */
  public handleClick = (val: any) => {
    const { history, location } = this.props;
    history.replace({
      pathname: `${location.pathname}`,
      search: `?page=${val}`
    } as any);
  };
  public render() {
    const { location } = this.props;
    const params = new URLSearchParams(location.search);
    const page = (params.get('page') as string) || '1';
    return (
      <div className="illegal-setting">
        <Tabs size="small" defaultActiveKey={page} onTabClick={this.handleClick}>
          <Tabs.TabPane tab="车牌识别配置" key="1">
            <LPRConfig />
          </Tabs.TabPane>
          <Tabs.TabPane tab="信息叠加配置" key="2">
            <InfoConfig />
          </Tabs.TabPane>
          <Tabs.TabPane tab="路口信息设置" key="3">
            <CrossingsInfoConfig />
          </Tabs.TabPane>
        </Tabs>
      </div>
    );
  }
}

export default withRouter(IllegalSetting);
