import React, { Component } from 'react';
import { Form, Button, Input, InputNumber, Row, Col, message } from 'antd';

import request from '@/request';

interface Data {
  roadId: string;
  roadName: string;
  roadDirection: string;
  deviceId: string;
  laneNumber: number;
  laneCount: number;
}
interface IProps {
  form: any;
}

interface IState {
  data: Data;
}

class CrossingsInfoConfig extends Component<IProps, IState> {
  public state = {
    data: {} as Data
  };
  /**
   * componentDidMount
   */
  public componentDidMount() {
    this.getData();
  }
  /**
   * getData
   */
  public getData() {
    request
      .config({
        url: '/settings/road'
      })
      .then((res: any) => {
        this.setState({
          data: res.data
        });
      });
  }
  /**
   * handleCancel
   */
  public handleCancel = () => {
    const { resetFields } = this.props.form;
    resetFields();
  };
  public handleSubmit = () => {
    const { getFieldsValue } = this.props.form;
    const data = getFieldsValue();
    request
      .config({
        url: `/settings/road`,
        type: 'post',
        data: data
      })
      .then((res: any) => {
        if (res.code === 0) {
          message.info('保存成功!');
          this.getData();
        } else {
          message.info(res.message);
        }
      });
  };
  public render() {
    const { getFieldDecorator } = this.props.form;
    const { data } = this.state;

    const formItemLayout = {
      labelCol: {
        sm: { span: 6 }
      },
      wrapperCol: {
        sm: { span: 18 }
      }
    };
    return (
      <div style={{ width: '50%' }}>
        <Form {...formItemLayout}>
          <Row>
            <Col span={16} offset={4}>
              <Form.Item label="路口id:">
                {getFieldDecorator('roadId', {
                  initialValue: data.roadId
                })(<Input placeholder="请输入id" allowClear={true} />)}
              </Form.Item>
              <Form.Item label="路口名称:">
                {getFieldDecorator('roadName', {
                  initialValue: data.roadName
                })(<Input placeholder="请输入路口名称" allowClear={true} />)}
              </Form.Item>
              <Form.Item label="路口方向:">
                {getFieldDecorator('roadDirection', {
                  initialValue: data.roadDirection
                })(<Input placeholder="请输入路口方向" allowClear={true} />)}
              </Form.Item>
              <Form.Item label="照相设备id:">
                {getFieldDecorator('deviceId', {
                  initialValue: data.deviceId
                })(<Input placeholder="请输入照相设备id" allowClear={true} />)}
              </Form.Item>
              <Form.Item label="车道号:">
                {getFieldDecorator('laneNumber', {
                  initialValue: data.laneNumber
                })(<Input placeholder="请输入车道号" allowClear={true} />)}
              </Form.Item>
              <Form.Item label="车道总数:">
                {getFieldDecorator('laneCount', {
                  initialValue: data.laneCount
                })(<InputNumber placeholder="请输入车道总数" />)}
              </Form.Item>
            </Col>
          </Row>

          <Row style={{ marginTop: 10 }}>
            <Col span={14} offset={10}>
              <Button icon="check" type="primary" style={{ marginRight: 80 }} onClick={this.handleSubmit}>
                确定
              </Button>
              <Button icon="close" onClick={this.handleCancel}>
                取消
              </Button>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}

export default Form.create({ name: 'CrossingsInfoConfig' })(CrossingsInfoConfig);
