import React, { Component } from 'react';
import { Form, Select, Button, Input, InputNumber, Row, Col, Checkbox, message } from 'antd';

import request from '@/request';

interface ItemData {
  minPlateWidth: number;
  maxPlateWidth: number;
  maxPlateOutput: number;
  plateLocationThreshold: number;
  plateRecognitionThreshold: number;
  priorityState: string;
  findAgriculturalPlate: boolean;
  findDoubleMilitaryPlate: boolean;
  findDoubleYellowPlate: boolean;
  findMilitaryPlate: boolean;
  findPersonalizedPlate: boolean;
  pickExample: boolean;
  saveImage: boolean;
}

interface IProps {
  form: any;
}

interface IState {
  data: ItemData;
}

class LPRconfig extends Component<IProps, IState> {
  public state = {
    data: {} as ItemData
  };
  /**
   * componentDidMount
   */
  public componentDidMount() {
    this.getData();
  }
  /**
   * getData
   */
  public getData() {
    request.config({ url: '/settings/plate' }).then((res: any) => {
      this.setState({ data: res.data });
    });
  }
  /**
   * handleReset
   */
  public handleReset = () => {
    const { resetFields } = this.props.form;
    request
      .config({
        url: `/settings/defaultPlate`
      })
      .then((res: any) => {
        this.setState(
          {
            data: res.data
          },
          () => {
            resetFields();
          }
        );
      });
  };
  /**
   * handleCanel
   */
  public handleCanel = () => {
    const { resetFields } = this.props.form;
    resetFields();
  };
  public handleSubmit = () => {
    const { getFieldsValue } = this.props.form;
    const data = getFieldsValue();
    request
      .config({
        url: `/settings/plate`,
        type: 'post',
        data: data
      })
      .then((res: any) => {
        if (res.code === 0) {
          message.info('保存成功!');
          this.getData();
        } else {
          message.info(res.message);
        }
      });
  };
  public render() {
    const { getFieldDecorator } = this.props.form;
    const { data } = this.state;

    const formItemLayout = {
      labelCol: {
        sm: { span: 10 }
      },
      wrapperCol: {
        sm: { span: 14 }
      }
    };
    return (
      <div style={{ width: '70%' }}>
        <Form {...formItemLayout}>
          <Row>
            <Col span={10}>
              <Form.Item label="车牌最小宽度:">
                {getFieldDecorator('minPlateWidth', {
                  initialValue: data.minPlateWidth
                })(<Input placeholder="请输入宽度" allowClear={true} />)}
              </Form.Item>
            </Col>
            <Col span={10} offset={2}>
              <Form.Item label="车牌最大宽度:">
                {getFieldDecorator('maxPlateWidth', {
                  initialValue: data.maxPlateWidth
                })(<Input placeholder="请输入宽度" allowClear={true} />)}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={10}>
              <Form.Item label="省份优先级:">
                {getFieldDecorator('priorityState', {
                  initialValue: data.priorityState
                })(<Input allowClear={true} />)}
              </Form.Item>
            </Col>
            <Col span={10} offset={2}>
              <Form.Item label="最多输出车牌个数:">
                {getFieldDecorator('maxPlateOutput', {
                  initialValue: data.maxPlateOutput
                })(
                  <Select placeholder="请选择" showSearch>
                    <Select.Option value={1}>1</Select.Option>
                    <Select.Option value={2}>2</Select.Option>
                    <Select.Option value={3}>3</Select.Option>
                    <Select.Option value={4}>4</Select.Option>
                    <Select.Option value={5}>5</Select.Option>
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={10}>
              <Form.Item label="车牌定位阈值:">
                {getFieldDecorator('plateLocationThreshold', {
                  initialValue: data.plateLocationThreshold
                })(<InputNumber min={0} placeholder="请输入阈值" />)}
              </Form.Item>
            </Col>
            <Col span={10} offset={2}>
              <Form.Item label="车牌识别阈值:">
                {getFieldDecorator('plateRecognitionThreshold', {
                  initialValue: data.plateRecognitionThreshold
                })(<InputNumber min={0} placeholder="请输入阈值" />)}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={8} offset={4}>
              <Form.Item>{getFieldDecorator('pickExample', { valuePropName: 'checked', initialValue: data.pickExample })(<Checkbox>隔行抽点</Checkbox>)}</Form.Item>
            </Col>
            <Col span={8} offset={4}>
              <Form.Item>{getFieldDecorator('saveImage', { valuePropName: 'checked', initialValue: data.saveImage })(<Checkbox>输入场图像</Checkbox>)}</Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={8} offset={4}>
              <Form.Item>
                {getFieldDecorator('findDoubleYellowPlate', { valuePropName: 'checked', initialValue: data.findDoubleYellowPlate })(<Checkbox>识别双层黄牌</Checkbox>)}
              </Form.Item>
            </Col>
            <Col span={8} offset={4}>
              <Form.Item>
                {getFieldDecorator('findPersonalizedPlate', { valuePropName: 'checked', initialValue: data.findPersonalizedPlate })(<Checkbox>识别个性化车牌</Checkbox>)}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={8} offset={4}>
              <Form.Item>{getFieldDecorator('findMilitaryPlate', { valuePropName: 'checked', initialValue: data.findMilitaryPlate })(<Checkbox>识别军用车牌</Checkbox>)}</Form.Item>
            </Col>
            <Col span={8} offset={4}>
              <Form.Item>
                {getFieldDecorator('findDoubleMilitaryPlate', { valuePropName: 'checked', initialValue: data.findDoubleMilitaryPlate })(<Checkbox>识别双层军用车牌</Checkbox>)}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col span={8} offset={4}>
              <Form.Item>
                {getFieldDecorator('findAgriculturalPlate', { valuePropName: 'checked', initialValue: data.findAgriculturalPlate })(<Checkbox>识别农用车牌</Checkbox>)}
              </Form.Item>
            </Col>
            <Col span={8} offset={4}>
              <Form.Item>
                <Button icon="redo" onClick={this.handleReset}>
                  恢复默认设置
                </Button>
              </Form.Item>
            </Col>
          </Row>
          <Row style={{ marginTop: 10 }}>
            <Col span={12} offset={8}>
              <Form.Item>
                <Button icon="check" type="primary" style={{ marginRight: 80 }} onClick={this.handleSubmit}>
                  确定
                </Button>
                <Button icon="close" onClick={this.handleCanel}>
                  取消
                </Button>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}

export default Form.create({ name: 'LPRconfig' })(LPRconfig);
